<template>
  <div class="welcome">
    <h1>易經</h1>
    <br>
    <h3>時間占禁忌</h3>
    <ul>
      <li>不怪不占</li>
      <li>不事不占</li>
      <li>不動不占</li>
    </ul>
    <br>
    <h1>用途：</h1>
    <ul>
      <li>小時：用按鈕時間，以小時為單位起卦。</li>
      <li>時刻：作夢占，用按鈕時間，以分為單位起卦。</li>
      <li>指定：指定時間起卦，以小時為單位。</li>
      <li>生日起卦：西元/西元靈數/民國/農曆生日起卦。</li>
      <li>代表各對你有意義的生日與對應運勢</li>
    </ul>
  </div>
 
</template>

<script>
export default {
  props:{
  },
  name: 'welcome',
  data () {
    return {
      
    }
  }, 
  methods : {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1, h2 {
  font-weight: normal;
  text-align: center;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  margin: 10px 10px;
}

</style>



