function _get_gram(year, month, day, hour){
    //取得上下卦
    //initialization variables
    var above = 0;
    var below = 0;
    var dynamic_line = 0;

    //calculate above and below
    above = ( year+ month+ day)%8;
    below = ( year+ month+ day+ hour)%8;

    //calculate dynamic_line
    dynamic_line = ( year+ month+ day+ hour)%6;

    //check
    if( above == 0 )
    { above = 8;}
    if( below == 0 )
    { below = 8;}
    if( dynamic_line ==0)
    { dynamic_line = 6 ;}

    var gram = {above, below, dynamic_line};
    console.log (above, below ,dynamic_line)
    
    return gram;
}

function _change_dynamic_line(lines){

    //set dynamic_line
    var dynamic_line = lines[6];
    //set six lines form input
    var first_line  = lines[0] ;
    var second_line = lines[1] ;
    var third_line  = lines[2] ;
    var forth_line  = lines[3] ;
    var fifth_line  = lines[4] ;
    var sixth_line  = lines[5] ;
    //change_line
    switch(dynamic_line)
    {
        case 0:
        case 6:
            sixth_line  = (sixth_line +1) %2;
            break;
        case 1:
            first_line  = (first_line +1) %2;
            break;
        case 2:
            second_line = (second_line +1) %2;
            break;
        case 3:
            third_line  = (third_line +1) %2;
            break;
        case 4:
            forth_line  = (forth_line +1) %2;
            break;
        case 5:
            fifth_line  = (fifth_line +1) %2;
            break;
    }
//        result[] = first_line;
//        result[] = second_line;
//        result[] = third_line;
//        result[] = forth_line;
//        result[] = fifth_line;
//        result[] = six_line;

    var result = [first_line, second_line, third_line, forth_line, fifth_line, sixth_line, dynamic_line];
    console.log ("dynamic lines",result)
    return result ;
}

function _get_six_line(above, below, dynamic_line) {
    //由上下卦得初爻、二三四五上爻
    //initialization variables
    var first_line = 0;
    var second_line = 0;
    var third_line = 0;
    var forth_line = 0;
    var fifth_line = 0;
    var sixth_line = 0;
    console.log ("get six lines", above, below, dynamic_line)

    /*
        上下卦餘序一二三爻對應
        乾(111)兌(112)離(121)震(122)巽(211)坎(212)艮(221)坤(222)
    */

    switch (below)
    {
        case 1 :
            first_line  = 1;
            second_line = 1;
            third_line  = 1;
            break;
        case 2 :
            first_line  = 1;
            second_line = 1;
            third_line  = 2;
            break;
        case 3 :
            first_line  = 1;
            second_line = 2;
            third_line  = 1;
            break;
        case 4 :
            first_line  = 1;
            second_line = 2;
            third_line	= 2;
            break;
        case 5 :
            first_line	= 2;
            second_line	= 1;
            third_line  = 1;
            break;
        case 6 :
            first_line 	= 2;
            second_line	= 1;
            third_line	= 2;
            break;
        case 7 :
            first_line 	= 2;
            second_line	= 2;
            third_line  = 1;
            break;
        case 8 :
            first_line	= 2;
            second_line	= 2;
            third_line	= 2;
            break;
    }
    switch (above)
    {
        case 1 :
            forth_line  = 1;
            fifth_line  = 1;
            sixth_line  = 1;
            break;
        case 2 :
            forth_line  = 1;
            fifth_line  = 1;
            sixth_line  = 2;
            break;
        case 3 :
            forth_line  = 1;
            fifth_line 	= 2;
            sixth_line  = 1;
            break;
        case 4 :
            forth_line  = 1;
            fifth_line  = 2;
            sixth_line	= 2;
            break;
        case 5 :
            forth_line	= 2;
            fifth_line	= 1;
            sixth_line  = 1;
            break;
        case 6 :
            forth_line 	= 2;
            fifth_line	= 1;
            sixth_line	= 2;
            break;
        case 7 :
            forth_line 	= 2;
            fifth_line	= 2;
            sixth_line  = 1;
            break;
        case 8 :
            forth_line	= 2;
            fifth_line	= 2;
            sixth_line	= 2;
            break;
    }
    var result = [first_line, second_line, third_line, forth_line, fifth_line, sixth_line, dynamic_line];
    console.log (result)
    return result;
}

