function _get_dream_gram(hour, minute, period){
       /*取得上下卦*/
    //initialization variables
    
    //calculate above and below
    var above = hour %8;
    var below = ( hour+ minute)%8;
    
    //calculate dynamic_line
    var dynamic_line = ( hour+ minute+ period)%6;
    
    //check 
    if( above === 0 )
    { above = 8;}
    if( below === 0 )
    { below = 8;}
    if( dynamic_line ===0)
    { dynamic_line = 6 ;}

    var gram={above, below, dynamic_line};
    
    return gram ;
}

function _spiritual_number_calculation(input){
    //靈數計算用function
    //initialization variables
    var output = 0;
    while( input >9){
        output += input %10;
        input = (input-input%10)/10;
    }
    output = output+ input;
    return output;
}

//
//此刻 指定 標準西歷
//
export function hour_diviation (time){
        // time : date
        // year : 1900~2200 
        // month : 1~12
        // day : 1~31
        // hour : 1~24
        //initialization variables
        var gram_name = [];
        var six_lines  = [];

        //set variables for input
        //input(type,bit number,allow range);
        var _time = new Date(time);
        var year    = _time.getFullYear();
        var month   = _time.getMonth()+1;
        var day     = _time.getDate();
        var hour 	= _time.getHours();
        console.log(year,month,day,hour)
        //取得上下卦、動爻
        var gram = _get_gram( year, month, day, hour);
        
        //設定上下卦、動爻
        var above = gram['above'];
        var below = gram['below'];
        var dynamic_line = gram['dynamic_line'];
        //設各爻陰陽
        six_lines = _get_six_line( above, below, dynamic_line);
        //取得本卦名
        gram_name.push(_six_lines_to_hexagram_name( six_lines));

        //動爻取卦
        var dynamic_gram = _change_dynamic_line( six_lines);

        //取得動爻名
        gram_name.push(_six_lines_to_hexagram_name(dynamic_gram));

        return [gram_name,_time];
}

