        day     = _spiritual_number_calculation(_time.getDate());
        hour 	= _spiritual_number_calculation(_time.getHours());
        console.log(year,month,day,hour)
        //取得上下卦、動爻
        gram = _get_gram( year, month, day, hour);
        
        //設定上下卦、動爻
