//
// 陰曆 
// input : 農曆日期
export function lunar_diviation(lunar){
    var gram_name =[];
    var lunar_year    = lunar[0];
    var lunar_month   = lunar[1];
    var lunar_day     = lunar[2];
    var lunar_time_period = getTimePeriod(lunar[3]);
    console.log("農曆日", lunar_year,lunar_month,lunar_day, lunar_time_period)

    //取得上下卦、動爻
    var gram = _get_gram( lunar_year, lunar_month, lunar_day, lunar_time_period);
    console.log("農曆日", gram)

    //設定上下卦、動爻
    var above = gram['above'];
    var below = gram['below'];
    var dynamic_line = gram['dynamic_line'];

    //設各爻陰陽
    var six_lines = _get_six_line( above, below, dynamic_line);
    
    //取得本卦名
    
    gram_name.push(_six_lines_to_hexagram_name( six_lines));

    //動爻取卦
    var dynamic_gram = _change_dynamic_line( six_lines);

    //取得動爻名
    gram_name.push(_six_lines_to_hexagram_name(dynamic_gram));

    return gram_name[0]+'之'+gram_name[1]+'卦';
}

function getTimePeriod(hour){
    var period=0;
    switch(hour){
        case 0:
            period =1;
            break;
        case 1:
        case 2:
            period =2;
            break;
        case 3:
        case 4:
            period =3;
            break;
        case 5:
        case 6:
            period =4;
            break;
        case 7:
        case 8:
            period =5;
            break;
        case 9:
        case 10:
            period =6;
            break;
        case 11:
        case 12:
            period =7;
            break;
        case 13:
        case 14:
            period =8;
            break;
        case 15:
        case 16:
            period =9;
            break;
        case 17:
        case 18:
            period =10;
            break;
        case 19:
        case 20:
            period =11;
            break;
        case 21:
        case 22:
            period =12;
            break;
        case 23:
            period =1;
            break;
    }
    return  period
}


