<template>
  <div class="lunar_cul">
    <h1>農曆生日</h1>
    生肖(農曆年):
    <select class="custom-select" id="Year" v-model="year">
        <option value=0 selected>Choose...</option>
        <option v-for="(i,key) in yearOption" :value="key+1">{{i}}</option>
    </select>
    月
    <select class="custom-select" id="Month" v-model="month">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 12" :value="i">{{i}}</option>
    </select>    
    日
    <select class="custom-select" id="Day" v-model="day">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 31" :value="i">{{i}}</option>
    </select>
    小時
    <select class="custom-select" id="hour" v-model="hour">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 24" :value="i-1">{{i-1}}</option>
    </select>
    結果 : <p >{{result}}</p> 
    <button @click='Lunar_cul'>計算</button>
  </div>
 
</template>

<script>
export default {

  name: 'lunar_cul',
  data () {
    return {
      result: '',
      yearOption:[
        '鼠(甲子、丙子、戊子、庚子、壬子)',
        '牛(乙丑、丁丑、己丑、辛丑、癸丑)',
        '虎(丙寅、戊寅、庚寅、壬寅、甲寅)',
        '兔(丁卯、己卯、辛卯、癸卯、乙卯)',
        '龍(戊辰、庚辰、壬辰、甲辰、丙辰)',
        '蛇(己巳、辛巳、癸巳、乙巳、丁巳)',
        '馬(庚午、壬午、甲午、丙午、戊午)',
        '羊(辛未、癸未、乙未、丁未、己未)',
        '猴(壬申、甲申、丙申、戊申、庚申)',
        '雞(癸酉、乙酉、丁酉、己酉、辛酉)',
        '狗(甲戌、丙戌、戊戌、庚戌、壬戌)',
        '豬(乙亥、丁亥、己亥、辛亥、癸亥)'
      ],
      year:0,
      month:0,
      day:0,
      hour:0
    }
  }, 
  methods : {
    Lunar_cul(){
      console.log(this.year,this.month,this.day,this.hour)
      if(this.year !=0 && this.month!=0 && this.day!=0 && typeof(this.hour)!='undefined'){
        this.result = this.yijing.lunar_diviation([this.year,this.month,this.day,this.hour])
      }
      else{
        alert('請輸入數值')
      }
    },
 
    range(min,max){
        var array = [],
        j = 0;
        for(var i = min; i <= max; i++){
          array[j] = i;
          j++;
        }
        return array;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



