<template>
  <div class="now_cul">
    <h1>此刻(小時占)</h1>
    結果 : <p >{{result}}</p>
    <button @click='now_cul'>計算</button>
    <p v-model="timestring">{{timestring}}</p>  </div>
 
</template>

<script>
export default {
  props:{
  },
  name: 'now_cul',
  data () {
    return {
      result: '',
      timestring:''
    }
  }, 
  methods : {
    now_cul(){
      var resultArray =this.yijing.hour_diviation(Date.now())
      var aresult =resultArray[0]
      this.result = aresult[0]+"之"+aresult[1]+"卦"
      var time = resultArray[1]
      this.timestring = time.getFullYear()+' '+
                    (time.getMonth()+1)+' '+
                   time.getDate()+' '+
                   time.getHours()+' '+
                   time.getMinutes();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '~bootstrap/scss/bootstrap.scss';

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



