<template>
  <div class="dream_cul">
    <h1>作夢用時刻占</h1>
    結果 : <p >{{result}}</p> 
    <button @click='dream_cul'>計算</button>
    <p v-model="timestring">{{timestring}}</p>
  </div>
 
</template>

<script>
export default {

  name: 'dream_cul',
  data () {
    return {
      result: '',
      timestring:''
    }
  }, 
  methods : {
    dream_cul(){
      var resultArray =this.yijing.dream_diviation()
      var result =resultArray[0]
      this.result = result[0]+"之"+result[1]
      var time = resultArray[1]
      this.timestring = time.getFullYear()+' '+
                         (time.getMonth()+1)+' '+
                   time.getDate()+' '+
                   time.getHours()+' '+
                   time.getMinutes();

    }
  },
 
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



