<template>
  <div class="spad_cul">
    <h1>西元靈數生日</h1>
    西元年:
    <select class="custom-select" id="Year" v-model="year">
        <option value=0 selected>Choose...</option>
        <option v-for="i in range(1900,2100)" :value="i">{{i}}</option>
    </select>
    月
    <select class="custom-select" id="Month" v-model="month">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 12" :value="i">{{i}}</option>
    </select>    
    日
    <select class="custom-select" id="Day" v-model="day">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 31" :value="i">{{i}}</option>
    </select>
    小時
    <select class="custom-select" id="hour" v-model="hour">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 24" :value="i-1">{{i-1}}</option>
    </select>
    結果 : <p >{{result}}</p> 
    <button @click='sp_cul'>計算</button>
  </div>
 
</template>

<script>
export default {

  name: 'spad_cul',
  data () {
    return {
      result: '',
      year:0,
      month:0,
      day:0,
      hour:0
    }
  }, 
  methods : {
    sp_cul(){
      if(this.year !==0 && this.month!==0 && this.day!==0 && typeof(this.hour)!='undefined'){
        var d = new Date();
        this.result = this.yijing.spad_diviation(new Date(this.year,this.month-1,this.day, this.hour))
      }else{
        alert('請輸入數值')
      }
    },
    range(min,max){
        var array = [],
        j = 0;
        for(var i = min; i <= max; i++){
          array[j] = i;
          j++;
        }
        return array;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



