import Vue from 'vue'
import Router from 'vue-router'
import now_cul from '@/components/now_cul'
import lunar_cul from '@/components/lunar_cul'
import roc_cul from '@/components/roc_cul'
import welcome from '@/components/welcome'
import sp_cul from '@/components/sp_cul'
import ad_cul from '@/components/ad_cul'
import dream_cul from '@/components/dream_cul'
import spad_cul from '@/components/spad_cul'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/now_cul',
      name: 'now_cul',
      component: now_cul
    },
    {
      path: '/sp_cul',
      name: 'sp_cul',
      component: sp_cul
    },
    {
      path: '/roc_cul',
      name: 'roc_cul',
      component: roc_cul
    },
    {
      path: '/',
      name: 'welcome',
      component: welcome
    },
    {
      path: '/lunar_cul',
      name: 'lunar_cul',
      component: lunar_cul
    },
    {
      path: '/dream_cul',
      name: 'dream_cul',
      component: dream_cul
    },
    {
      path: '/ad_cul',
      name: 'ad_cul',
      component: ad_cul
    },
    {
      path: '/spad_cul',
      name: 'spad_cul',
      component: spad_cul
    }
  ]
})



